import React, { Component } from 'react';

const Problematic = (errThrow) => {
  if (errThrow) {
    console.log('Problematic');
    throw (new Error(' !'));
  } else {
    return (
      <div>

      </div>
    );
  }
};

class LifeCycle extends Component {
  state = {
    number: this.props.value
  }
  // constructor -   
  constructor(props) {
    super(props);
    console.log('constructor');
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    //  setState      props   
    //   state    .
    console.log('getDerivedStateFromProps');
    if (nextProps.value !== prevState.value) {
      return { value: nextProps.value };
    }
    return null; // null        
  }
  /*
  //  X 
  componentWillMount() {
    console.log('componentWillMount (deprecated)');
  }
  */
  // componentDidMount -      
  componentDidMount() {
    // ,       axios, fetch   
    // ajax  , DOM       .
    console.log('componentDidMount');
  }

  // render    ( return true;)
  shouldComponentUpdate(nextProps, nextState) {
    // state.number   false; render() X
    console.log('shouldComponentUpdate');
    if (nextState.number === this.state.number && !nextState.errorBtn) return false;
    return true;
  }

  // getSnapshotBeforeUpdate() -  DOM    
  getSnapshotBeforeUpdate(prevProps, prevState) {
    //  return componentDidUpdate  param snapshot .
    console.log('getSnapshotBeforeUpdate');
    return 'fromSnapshotBeforeUpdate';
  }
  // componentDidUpdate - component Updated 
  componentDidUpdate(prevProps, prevState, snapshot) {
    console.log('componentDidUpdate',', snapshot: ',snapshot);
  }
  /*
  //  X
  componentWillUpdate(nextProps, nextState) {
    console.log('componentWillUpdate');
  }
  */

  /**************    *******************/
  componentDidCatch(error, info) {
    console.log('componentDidCatch');
    this.setState({
      error: true
    });
  }

  handleChange = () => {
    this.setState({
      number: this.state.number + 1
    });
  }

  handleStay = () => {
    this.setState({
        number: this.state.number
      });
  }

  handleError = () => {
    console.log('handleError');
    this.setState({
        errorBtn: true
      });
  }
  
  render() {
    console.log('render');
    if (this.state.error) return (<h1>!</h1>);
    return (
      <div>
        <h1>3. Component Life Cycle</h1>
        <div>Number  : {this.state.number}</div>
        <button onClick={this.handleChange}>state </button>
        <button onClick={this.handleStay}>state </button>
        <button onClick={this.handleError}>Error </button>
        { this.state.errorBtn && <Problematic errThrow='true'/> }
        <div>
          <p><b>Componet  Life Cycle</b></p>
          <ol> 
            <li>constructor</li>
            <li>getDerivedStateFromProps</li>
            <li>render</li>
            <li>componentDidMount</li>
          </ol>
          <p><b>Componet  Life Cycle</b></p>
          <ol> 
            <li>getDerivedStateFromProps</li>
            <li>shouldComponentUpdate</li>
            <li>return ture , return false  </li>
            <li>render</li>
            <li>getSnapshotBeforeUpdate</li>
            <li>componentDidUpdate</li>
          </ol>
        </div>
      </div>
    );
  }
}

LifeCycle.defaultProps = {
  value: 1
}

export default LifeCycle;


