import React, { Component } from 'react';

class MyName extends Component {
  /*
  // defaultProps  (1)
  static defaultProps = {
    name: ''
  }
  */
  render() {
    return (
      <div>
        !   <b>{this.props.name}</b> . ( )
      </div>
    );
  }
}

// defaultProps  (2)
MyName.defaultProps = {
  name: 'defaultProps'
};

export default MyName;


