import React, { Component } from 'react';

class Counter extends Component {
  /*
  // classfield X
  // constructor ,  bind   ,   
  constructor(props) {
    super(props);
    this.state = {
      number: props.value
    }
    this.handleIncrease = this.handleIncrease.bind(this);
    this.handleDecrease = this.handleDecrease.bind(this);
  }
  handleIncrease() {
    this.setState({
      number: this.state.number + 1
    });
  }
  handleDecrease() {
    this.setState({
      number: this.state.number - 1
    });
  }
  */

  // classfield 
  // constructor , bind     
  state = {
    number: this.props.value,
    foo: {
      bar: 0,
      foobar: 1
    }
  }
  //    this  
  handleIncrease = () => {
    this.setState({
      number: this.state.number + 1
    });
  }
  handleDecrease = () => {
    // setState    
    this.setState((state) => ({
      number: state.number - 1
    })
    );
    /*
    //  
    this.setState(
      ({ number }) => ({
        number: 1
      })
    );
    */
    /*
    this.setState({
      number: this.state.number - 1
    });
    */
  }

  settingStateOriginal = () => {
    this.setState({
      number: this.props.value,
      foo: {
        bar: 0,
        foobar: 1
      }
    });
  }

  settingState = () => {
    this.setState({
      foo: {
        foobar: 2 // foobar    ,  foo  .
      }
    });
  }

  settingState2 = () => {
    this.setState({
      foo: {
        ...this.state.foo,
        foobar: 2 // foobar !  foo   X
      }
    });
  }

  render() {
    return (
      <div>
        <h1>2. state </h1>
        <div>: {this.state.number}</div>
        <button onClick={this.handleIncrease}>+</button>
        <button onClick={this.handleDecrease}>-</button>
        <br></br>
        <br></br>
        <div>
          State: number : {this.state.number},
                   foo : bar : {this.state.foo.bar},
                   foo : foobar : {this.state.foo.foobar}.
        </div>
        <button onClick={this.settingStateOriginal}>InitState</button>
        <br></br>
        <button onClick={this.settingState}>SetState(foo.foobar:2)</button>
        <br></br>
        <button onClick={this.settingState2}>SetState(...this.state.foo, foo.foobar:2)</button>
      </div>
    );
  }
}
// defaultProps  
Counter.defaultProps = {
  value: 1
};
export default Counter;


