import React from 'react';

//   (1)
function MyName(props) {
  return (
    <div>
      !   <b>{props.name}</b> . ( )
    </div>
  );
};

/*
//   (2)
const MyName = (props) => {
  return (
    <div>
      !   <b>{props.name}</b> . ( )
    </div>
  );
};
*/
/*
//   (3)
const MyName = ({ name }) => {
  return (
    <div>
      !   <b>{name}</b> . ( )
    </div>
  );
};
*/

// defaultProps 
MyName.defaultProps = {
  name: 'defaultProps'
};


export default MyName;


